.fullGameBoard {
    position: absolute;
    top: 175px;
    left: 33%;
}

.board {
    float: left;
    /*position: relative;*/
    height: 386px;
    width: 386px;
    border-style: solid;
    border-width: 9px;
    border-color: #DEB887;
}

div.square {
    float: left;
    position: relative;
    width: 46px;
    height: 46px;
    border-width: 2px;
    border-right-style: solid;
    border-bottom-style: solid;
    border-color: #444;
}


/* the bottom row */

div.square:nth-last-child(-n + 8) {
    border-bottom-style: none;
}


/* the sides */

div.square:nth-child(8n) {
    border-right-style: none;
}

div.square.light {
    background-color: #ccc;
}

div.square.dark {
    background-color: #333;
}

div.piece {
    float: left;
    position: absolute;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    z-index: 10;
    margin: 6px;
    margin-top: 5px;
}

div.piece.playerOne {
    background-color: purple;
}

div.piece.playerTwo {
/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f5ab95+0,f06034+0,d53601+19,d53601+19,d53601+36,e87000+55,b62d00+75 */
    background: rgb(245,171,149); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover, rgba(245,171,149,1) 0%,rgba(240,96,52,1) 0%,rgba(213,54,1,1) 19%,rgba(213,54,1,1) 19%,rgba(213,54,1,1) 36%,rgba(232,112,0,1) 55%,rgba(182,45,0,1) 75%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-radial-gradient(center, ellipse, rgba(245,171,149,1) 0%,rgba(240,96,52,1) 0%,rgba(213,54,1,1) 19%,rgba(213,54,1,1) 19%,rgba(213,54,1,1) 36%,rgba(232,112,0,1) 55%,rgba(182,45,0,1) 75%);
    background: -o-radial-gradient(center, ellipse, rgba(245,171,149,1) 0%,rgba(240,96,52,1) 0%,rgba(213,54,1,1) 19%,rgba(213,54,1,1) 19%,rgba(213,54,1,1) 36%,rgba(232,112,0,1) 55%,rgba(182,45,0,1) 75%);
