    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f5ab95', endColorstr='#b62d00',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

div.piece.playerOne.king {
    border: 6px solid purple;
    width: 32px;
    height: 32px;
}

div.piece.playerTwo.king {
    border: 6px solid purple;
    width: 32px;
    height: 32px;
}

h1 {
    color: white;
}


